:root {
  --main_color_1: green;
  --main_color_2: #cc9a29;
  --main_bg_color_1: #00800054;
  --main_bg_color_11: #00800080;
  --main_bg_color_111: #008000a8;
  --main_bg_color_2: #cc9a2954;
  --main_bg_color_22: #cc9a2980;
  --main_bg_color_222: #cc9a29a8;
  --floreil-font-family: Georgia, "Times New Roman", Times, serif;
}

body {
  font-family: var(--floreil-font-family);
  min-width: 280px;
}

section {
  padding: 25px 0;
}

header, .modal-header {
  background: linear-gradient(to bottom, #00000050 0%, var(--main_bg_color_222) 100%), url("scott-webb-pots-in-a-row22.2d37ed2f.jpg");
  background-size: auto 100%;
}

.modal-footer {
  background: linear-gradient(to bottom, var(--main_bg_color_222) 0%, #00000050 100%), url("scott-webb-pots-in-a-row33.8e1e1063.jpg");
  background-size: auto 100%;
}

#search_section, #prefooter_section, .intersection {
  border-bottom: solid 2px var(--main_color_1);
  border-top: solid 2px var(--main_color_1);
  z-index: 1049;
  background: linear-gradient(to right, var(--main_bg_color_11) 0%, var(--main_bg_color_22) 50%, var(--main_bg_color_11) 100%), url("joe-woods-4Zaq5xY5M_c-unsplash.898620c6.jpg");
  background-size: cover;
  background-attachment: fixed;
  padding-top: 3px;
  padding-bottom: 3px;
}

#video_section, #ads_carousel {
  background: linear-gradient(to bottom, var(--main_bg_color_22) 0%, var(--main_bg_color_11) 100%);
}

.modal-body {
  background: linear-gradient(to bottom, var(--main_bg_color_22) 0%, var(--main_bg_color_11) 50%, var(--main_bg_color_22) 100%);
}

#reset_password_section, #signup_section, #login_section {
  background: linear-gradient(to bottom, var(--main_bg_color_1) 0%, var(--main_bg_color_2) 100%);
}

#commerciale, #footer {
  background: linear-gradient(to bottom, var(--main_bg_color_11) 0%, var(--main_bg_color_22) 100%);
}

h1 {
  text-shadow: 1px 1px 2px #000;
}

#logo {
  background: radial-gradient(#ffffff75 0%, #cc9a2900 85%);
}

.carousel-caption {
  background: radial-gradient(#000 0%, #0000 75%);
}

#header a, #search_section a, #prefooter_section a {
  text-decoration: none;
}

.card {
  background-color: #0000;
  border: #0000;
}

.card-img, .card-img-overlay {
  box-shadow: 0 0 4px 1px #000;
}

.florei_vert_separator {
  background-color: var(--main_color_1);
  width: 5px;
  box-shadow: 1px 1px 2px #000;
}

.florei_brun_separator {
  background-color: var(--main_color_2);
  border: solid thin var(--main_color_1);
  width: 5px;
  box-shadow: 0 0 2px #000;
}

.intersection {
  height: 20px;
}

.text-shadow {
  text-shadow: 0 0 2px #000;
}

#search {
  box-shadow: 0 0 3px #000;
}

.rotatable_y_180 {
  opacity: 1;
  transition: all .5s;
  transform: rotateY(0);
}

.rotatable_y_0 {
  color: #000;
  opacity: 0;
  transition: all .5s;
  transform: rotateY(180deg);
}

.rot_y_180_toggler:hover .rotatable_y_180 {
  opacity: 0;
  transition: all .5s;
  transform: rotateY(180deg);
}

.rot_y_180_toggler:hover .rotatable_y_0 {
  opacity: 1;
  transition: all .5s;
  transform: rotateY(0);
}

.rotatable_z_0 {
  transition: all .15s ease-in-out;
  transform: rotateZ(0);
}

.rotatable_z_90 {
  transition: all .15s ease-in-out;
  transform: rotateZ(90deg);
}

.rotatable_z_180 {
  transition: all .15s ease-in-out;
  transform: rotateZ(180deg);
}

.embed-container {
  height: 0;
  max-width: 100%;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
}

.embed-container iframe, .embed-container object, .embed-container embed {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.color_1, .color_2 {
  color: var(--main_color_1);
}

.bg_color_1 {
  background-color: var(--main_bg_color_1);
}

.bg_color_11 {
  background-color: var(--main_bg_color_11);
}

.bg_color_2 {
  background-color: var(--main_bg_color_2);
}

.bg_color_22 {
  background-color: var(--main_bg_color_22);
}

.huge {
  height: 52px;
  width: 52px;
}

.big {
  height: 44px;
  width: 44px;
}

.medium {
  height: 36px;
  width: 36px;
}

.small {
  height: 28px;
  width: 28px;
}

.btn {
  box-shadow: 0 0 3px #000;
}

.btn:hover {
  box-shadow: 0 0 4px 1px #000;
}

.btn:hover .text-shadow {
