  --bs-btn-hover-color: var(--main_color_1);
  --bs-btn-hover-bg: var(--main_bg_color_222);
  --bs-btn-hover-border-color: var(--main_bg_color_222);
  --bs-btn-active-color: var(--main_color_2);
